/* Reset des marges et des paddings */
body,
html,
div,
ul,
li,
button,
select {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-scroll-behavior: smooth;
}


/* Appliquer une police et une couleur de fond globale */
body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0; /* Couleur de fond globale */
}

/* Style du conteneur principal */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

/* Style du conteneur principal de l'application */
.App-container {
  margin-top: 140px; /* Ajustez la marge supérieure en fonction de la hauteur du header */
}

.logo img {
  float: left;
  width: 120px; /* Ajustez la largeur selon vos besoins */
  height: auto; /* La hauteur s'ajustera automatiquement pour conserver les proportions d'origine */
  margin-left: 10px; /* Ajoutez une marge à droite pour l'espace */
}

/* Ajouter ces styles pour aligner les <p> dans header-info sur la même ligne et les répartir sur toute la largeur */
.header-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin-left: 50px;
}


/* Header avec couleur de fond bleue */
  .header-container {
    background-color: #9da9b3;
    transition: background-color 1s ease;
    color: white;
    text-align: center;
    padding: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%; /* Occupe toute la largeur de la fenêtre */
    padding: 10px 0;
    z-index: 100;
  }

/* Style du sélecteur de codex */
select {
  padding: 5px;
  font-size: 16px;
  margin-right: 10px;
}

i {
  font-size: 10pt;
}

/* Style des colonnes (divs) */
.container {
  display: flex;
  flex-wrap: wrap; /* Permet aux colonnes de passer à la ligne si elles ne tiennent pas toutes sur une seule ligne */
  justify-content: space-between;
  margin-top: 20px;
}

.column{
  flex: 1;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  margin-right: 10px;
  margin-bottom: 20px; /* Ajout d'un espace entre les colonnes */
}

.column-center{
  position: sticky;
  top:155px;
  flex: 1;
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  margin-right: 10px;
  margin-bottom: 20px; /* Ajout d'un espace entre les colonnes */
  max-height: 100vh;
}

/* Style de la colonne de droite */
.column-right {
  max-width: 700px; /* Définissez la largeur maximale souhaitée */
  flex: 1; /* Permet à la colonne de droite de croître en fonction de son contenu */
  padding: 20px;
  background-color: #fff;
  border: 1px solid #ddd;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px; /* Ajout d'un espace entre les colonnes */
 
}

.card {
  margin-bottom: 10px;
  border: 1px solid #ddd;
  padding: 10px;
  transition: border 0.2s ease-in-out;
  display: grid; /* Utilisation de CSS Grid pour la carte */
  grid-template-rows: auto auto; /* Deux colonnes, la première s'ajuste au contenu, la deuxième a une largeur fixe */
  align-items: center; /* Centrer verticalement les éléments */
}


/* Style du titre de la carte et du bouton */
.card-header {
  display: flex; /* Utilisation de Flexbox pour le titre et le bouton */
  align-items: center; /* Centrer verticalement les éléments */
  justify-content: space-between; /* Espace équitablement les éléments horizontalement */
  flex: 1; /* Le titre prendra l'espace restant */
}

/* Style du titre de la carte */
.card h2 {
  font-size: 18px;
  margin-right: 10px;
  grid-row: 1; /* Définir la première colonne pour le titre */
  white-space: nowrap;
}

/* Au survol d'une carte, augmenter l'épaisseur de la bordure */
.card:hover {
  border: 2px solid #2196F3; /* Bordure bleue au survol */
}

/* Style des options (ul) */
ul {
  list-style-type: none;
  padding-left: 0;
  white-space: nowrap
}

/* Style de la carte avec la valeur la plus élevée */
.bold-text {
  font-weight: bold;
}

/* Style des boutons */
button {
  background-color: transparent; /* Boutons avec fond transparent */
  color: #21f383; /* Couleur de texte bleue */
  border: none;
  padding: 5px 10px;
  margin-left: 5px;
  cursor: pointer;
  border-radius: 50%; /* Boutons ronds */
  font-weight: bold;
  font-size: 18px; /* Taille du texte des boutons */
  transition: color 0.2s ease-in-out; /* Transition pour la couleur du texte */
}

/* Bouton de suppression (X) */
.remove-button {
  color: #FF5722;
}

/* Bouton de réduction (-) */
.minus-button {
  color: #FF5722; 
}

/* Au survol des boutons, changer la couleur du texte */
button:hover {
  color: #3d3d3d; 
}

/* Style du container du titre liste */
.liste-header {
  text-align: left;
  margin-bottom: 5px;
  display: flex;
    justify-content: space-between;
}


/* Style du bouton pour supprimer toutes les cartes */
.remove-all-button-container {
  text-align: center;
  margin-bottom: 5px;
}

.remove-all-button {
  background-color: #FF5722;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s, box-shadow 0.1s;
}

.remove-all-button:hover {
  background-color: #D32F2F;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.print-header {
  display: flex;
  justify-content: space-between;
  background-color: #2196F3;
  color: white;
  padding: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 50px;
  left: 0;
  width: 100%;
  z-index: 100;
}

.print-button-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 1;
}

.print-button {
  background-color: transparent;
  color: white;
  border: none;
  padding: 1px;
  cursor: pointer;
  font-size: 28px;
  transition: color 0.2s ease-in-out;
}

.print-button:hover {
  color: #3d3d3d;
}

/* Style du bouton "Ajouter une formation" */
.add-formation-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.add-formation-button {
  background-color: rgb(44, 204, 210);
  color: white;
  border: none;
  border-radius: 4px; /* Bordures arrondies */
  padding: 10px 20px;
  cursor: pointer;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* Ombre légère */
  transition: background-color 0.1s, box-shadow 0.1s;
}

.add-formation-button:hover {
  background-color: rgb(0, 168, 154);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2); /* Ombre légère au survol */
}

.selected {
  border: 2px solid #007bff; /* Exemple de bordure en bleu pour mettre en évidence la carte sélectionnée */
  background-color: #f0f0f0; /* Exemple de couleur de fond pour mettre en évidence la carte sélectionnée */
}

/* Style pour l'info-bulle */
[data-title]:hover:after {
  content: attr(data-title);
  position: absolute;
  background: #333;
  color: #fff;
  padding: 5px;
  border-radius: 3px;
  font-size: 14px;
  white-space: nowrap;
}

/* Style du sélecteur d'options */
select {
  padding: 5px;
  font-size: 16px;
  margin-right: 10px;
  margin-left: 10px;
  background-color: #fff; /* Couleur de fond */
  color: #333; /* Couleur du texte */
  border: 1px solid #ddd; /* Bordure */
  border-radius: 4px; /* Coins arrondis */
  appearance: none; /* Masquer la flèche du sélecteur par défaut sur certains navigateurs */
}

/* Style lorsqu'un sélecteur est survolé */
select:hover {
  border-color: #007bff; /* Bordure bleue au survol */
}

/* Style lorsqu'un sélecteur est activé (focus) */
select:focus {
  outline: none; /* Supprimer la bordure de focus par défaut */
  box-shadow: 0 0 5px #007bff; /* Ombre légère bleue lorsqu'il est activé */
}

/* Style pour les options dans le menu déroulant */
select option {
  background-color: #fff; /* Couleur de fond des options */
  color: #333; /* Couleur du texte des options */
}

/* Style pour les options survolées dans le menu déroulant */
select option:hover {
  background-color: #007bff; /* Couleur de fond des options au survol */
  color: #fff; /* Couleur du texte des options au survol */
}


@media print {
  /* Styles spécifiques à l'impression */
  @page {
    size: A4; /* Définir la taille de la page A4 */
    margin: 20mm; /* Définir les marges de la page */
  }

  body {
    margin: 0; /* Réinitialiser les marges du corps */
  }

  .visible-on-print {
    display: block !important;
  }

  .no-print {
    display: none !important;
  }

  /* Ajuster la largeur du contenu pour remplir la page */
  .container,
  .column,
  .codex,
  .column-right {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }

  .centered-header{
align-items: center;
  }

  /* Ajoutez d'autres styles spécifiques à l'impression au besoin */
}

@media screen and (max-width: 600px) {
  /* Modifier la disposition ou le style pour les écrans plus petits ici */
  .header-container {
    padding: 5px; /* Réduisez l'espacement dans le header par exemple */
    font-size: 12px; /* Réduisez la taille de la police pour les écrans plus petits */
  }

  .header-info{
    margin-left: 1px;

  }

  .container {
    flex-direction: column; /* Affichez les colonnes les unes en dessous des autres */
    margin: 5px;
    max-width: 400px;
    word-wrap: break-word;
  }
select{
  font-size: 10px;
}

  .column-center,
  .column,
  .column-right {
    font-size: 11px;
    width: 100%; /* La largeur totale pour les écrans plus petits */
    margin-right: 0; /* Aucune marge à droite pour les colonnes */
    position: unset;
    margin: 5px;
    padding: 10px;
    }

  .column-center-content{
    max-width: 100%;
  overflow: hidden;
  
  }

  ul{
    white-space: break-spaces;
  }

  /* ... autres ajustements pour les écrans plus petits ... */
}

